import React, { useState } from 'react';
import { Navbar, Nav, Button, NavDropdown, Form, FormControl, Jumbotron } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import { Link, Route, Switch } from 'react-router-dom';
import DetailInformation from './Detail';

//********리액트 폴더를 생성한 후 폴더를 열고 들어가서 부트스트랩 설치 코드를 입력해야 오류가 안난다
function App() {

  let [shoes, shoesChange] = useState(Data);
  return (

    < div className="App" >
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link><Link to="/">Home</Link></Nav.Link>
            <Nav.Link ><Link to="/detail">Detail</Link></Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>


      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>30% SeasonOff</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
              {
                shoes.map((a, i) => {
                  return (
                    <Windows shoes={shoes[i]} i={i} key={i} />
                  )
                })
              }

            </div>
          </div>
        </Route>
        <Route path="/detail/:id">
          <DetailInformation shoes={shoes} />
        </Route>


        {/* <Route path="/:id">
          <div>아무거나 적은 것.</div>
        </Route> */}
        {/* 스위치를 써서 중복을 방지해 안보이게 된다 */}
      </Switch>
    </div >
  );
}

function Windows(props) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.shoes.id + 1) + '.jpg'} width="100%" />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
    </div>
  );
}





export default App;
